import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Layout from "../../../components/layout/Layout";
import "./BusinessProcessesPage.css";

export default function BusinessProcessesPage() {
  const navigate = useNavigate();

  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [q, setQ] = useState("");
  const [error, setError] = useState("");

  const load = async () => {
    setLoading(true);
    setError("");
    try {
      // TODO:    ,    
      // : const r = await api.get("/business-processes", { params: { q } });
      // setItems(r.data || []);
      setItems([]); //    
    } catch (e) {
      setError(e?.response?.data?.message || "   ");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    load();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onCreate = () => {
    navigate("/business-processes/new");
  };

  const filtered = q.trim()
    ? items.filter((x) => (x.name || "").toLowerCase().includes(q.toLowerCase()))
    : items;

  return (
    <Layout title=" ">
      <div className="bp">
        <div className="bp-head">
          <h1> </h1>
          <div className="bp-actions">
            <input
              className="input"
              placeholder="  "
              value={q}
              onChange={(e) => setQ(e.target.value)}
            />
            <button className="btn primary" onClick={onCreate}>+  </button>
          </div>
        </div>

        {error && <div className="bp-error">{error}</div>}

        {/*   */}
        {!loading && filtered.length === 0 && (
          <div className="card bp-empty">
            <div className="bp-empty__icon"></div>
            <div className="bp-empty__title">   </div>
            <div className="bp-empty__text">
                   ,   .    .
            </div>
            <div className="bp-empty__actions">
              <button className="btn primary" onClick={onCreate}> </button>
              <button className="btn ghost" onClick={load}></button>
            </div>
          </div>
        )}

        {/*  (   ) */}
        {loading && <div className="bp-loading"></div>}

        {!loading && filtered.length > 0 && (
          <div className="bp-list">
            {filtered.map((bp) => (
              <div key={bp.id} className="bp-item card">
                <div className="bp-item__top">
                  <div className="bp-item__title">{bp.name}</div>
                  <div className="bp-item__actions">
                    <button
                      className="btn xs ghost"
                      title=""
                      onClick={() => navigate(`/business-processes/${bp.id}/edit`)}
                    >
                      
                    </button>
                    <button className="btn xs danger" title=""></button>
                  </div>
                </div>
                <div className="bp-item__meta">
                  : {bp.updated_at ? new Date(bp.updated_at * 1000).toLocaleString() : ""}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </Layout>
  );
}

