import React, { useEffect, useMemo, useState } from "react";
import Layout from "../../../components/layout/Layout";
import "./TemplatesPage.css";
import VoiceInput from "../../../shared/components/VoiceInput";
import {
  listTemplates,
  createTemplate,
  updateTemplate,
  deleteTemplate,
} from "../../templates/api/templatesApi";
import { getResults } from "../../results/api/results";

const FIELD_LABELS = {
  title: " ",
  expected_result: " ",
  result: "",
  type: "",
  planned_time: " ",
  actual_time: " ",
  manager: " ",
  comments: "",
  resultId: "",
  repeat: "",
  responsible: "",
  start_date: " ",
  end_date: " ",
  tags: "",
  enabled: "",
};

const defaultFlags = {
  title: true,
  expected_result: true,
  result: true,
  type: true,
  planned_time: true,
  actual_time: true,
  manager: true,
  comments: true,
  resultId: true,
  repeat: true,
  responsible: true,
  start_date: true,
  end_date: true,
  tags: true,
  enabled: true,
};

const defaultPayload = {
  title: "",
  expected_result: "",
  result: "",
  type: " ",
  planned_time: "00:00",
  actual_time: "00:00",
  manager: "",
  comments: "",
  resultId: "",
  repeat: { type: "none", interval: 1 },
  responsible: "",
  start_date: "",
  end_date: "",
  tags: "",
  enabled: true,
};

function FlagsBadge({ flags = {} }) {
  const enabled = Object.keys(flags).filter((k) => flags[k]);
  if (!enabled.length) return <span className="badge muted"></span>;
  return (
    <div className="flags">
      {enabled.map((k) => (
        <span key={k} className="badge">
          {FIELD_LABELS[k] || k}
        </span>
      ))}
    </div>
  );
}

function RowActions({ onEdit, onDelete, busy }) {
  return (
    <div className="row-actions">
      <button className="btn xs ghost" onClick={onEdit} disabled={busy} title="">
        
      </button>
      <button
        className="btn xs danger"
        onClick={onDelete}
        disabled={busy}
        title=""
      >
        
      </button>
    </div>
  );
}

export default function TemplatesPage() {
  const [items, setItems] = useState([]);
  const [q, setQ] = useState("");
  const [loading, setLoading] = useState(false);
  const [busyId, setBusyId] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [form, setForm] = useState({
    id: null,
    name: "",
    flags: { ...defaultFlags },
    payload: { ...defaultPayload },
  });
  const [error, setError] = useState("");
  const [results, setResults] = useState([]);

  const load = async () => {
    setLoading(true);
    setError("");
    try {
      const data = await listTemplates();
      setItems(Array.isArray(data) ? data : []);
    } catch (e) {
      setError(e?.response?.data?.message || "   ");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    load();
    getResults({ status: "active" })
      .then((data) => setResults(data.results || []))
      .catch(() => {});
  }, []);

  const filtered = useMemo(() => {
    if (!q.trim()) return items;
    const s = q.toLowerCase();
    return items.filter((x) => (x.name || "").toLowerCase().includes(s));
  }, [items, q]);

  const openCreate = () => {
    setForm({
      id: null,
      name: "",
      flags: { ...defaultFlags },
      payload: { ...defaultPayload },
    });
    setModalOpen(true);
  };

  const openCreateFromResult = (result) => {
    setForm({
      id: null,
      name: result?.title || "",
      flags: { ...defaultFlags },
      payload: { ...defaultPayload, resultId: result?.id || "" },
    });
    setModalOpen(true);
  };

  const openEdit = (t) => {
    setForm({
      id: t.id,
      name: t.name || "",
      flags: { ...defaultFlags, ...(t.flags || {}) },
      payload: {
        title: t.payload?.title ?? "",
        expected_result: t.payload?.expected_result ?? "",
        result: t.payload?.result ?? "",
        type: t.payload?.type ?? " ",
        planned_time: t.payload?.planned_time ?? "00:00",
        actual_time: t.payload?.actual_time ?? "00:00",
        manager: t.payload?.manager ?? "",
        comments: t.payload?.comments ?? "",
        resultId: t.payload?.resultId ?? "",
        repeat: t.payload?.repeat ?? { type: "none", interval: 1 },
        responsible: t.payload?.responsible ?? "",
        start_date: t.payload?.start_date ?? "",
        end_date: t.payload?.end_date ?? "",
        tags: t.payload?.tags ?? "",
        enabled: t.payload?.enabled ?? true,
      },
    });
    setModalOpen(true);
  };

  const handleVoiceTemplate = (data) => {
    setForm((s) => ({
      ...s,
      name: data.name || s.name,
      flags: { ...s.flags, ...(data.flags || {}) },
      payload: {
        ...s.payload,
        ...(data.payload || {
          title: data.title,
          expected_result: data.expected_result,
          result: data.result,
          type: data.type,
          planned_time: data.planned_time,
          actual_time: data.actual_time,
          manager: data.manager,
          comments: data.comments,
          resultId: data.resultId,
          repeat: data.repeat,
          responsible: data.responsible,
          start_date: data.start_date,
          end_date: data.end_date,
          tags: data.tags,
          enabled: data.enabled,
        }),
      },
    }));
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    setError("");
    try {
      const payload = {
        name: form.name.trim(),
        flags: form.flags,
        payload: {
          ...form.payload,
          repeat:
            form.payload.repeat?.type === "custom"
              ? {
                  type: "custom",
                  interval: Number(form.payload.repeat.interval || 1),
                }
              : { type: form.payload.repeat?.type || "none", interval: 1 },
        },
      };
      if (!payload.name) {
        setError("  ");
        return;
      }
      if (form.id) {
        setBusyId(form.id);
        await updateTemplate(form.id, payload);
      } else {
        setBusyId(-1);
        await createTemplate(payload);
      }
      setModalOpen(false);
      setBusyId(null);
      await load();
    } catch (e2) {
      setBusyId(null);
      setError(e2?.response?.data?.message || " ");
    }
  };

  const onDelete = async (id) => {
    if (!window.confirm(" ?   .")) return;
    setBusyId(id);
    try {
      await deleteTemplate(id);
      await load();
    } catch (e) {
      setError(e?.response?.data?.message || "   ");
    } finally {
      setBusyId(null);
    }
  };

  const setPayload = (key, val) =>
    setForm((s) => ({ ...s, payload: { ...s.payload, [key]: val } }));

  return (
    <Layout>
      <div className="templates-page">
        <div className="tp-main">
          <div className="tp-head">
            <h1></h1>
            <div className="tp-actions">
              <input
                className="input"
                placeholder="  "
                value={q}
                onChange={(e) => setQ(e.target.value)}
              />
              <button className="btn primary" onClick={openCreate}>
                +  
              </button>
            </div>
          </div>

          {error && <div className="tp-error">{error}</div>}

          <div className="tp-table card">
            <div className="tp-row tp-row--head">
              <div className="c-name"></div>
              <div className="c-flags"> </div>
              <div className="c-meta"></div>
              <div className="c-actions"></div>
            </div>
            {loading ? (
              <div className="tp-empty"></div>
            ) : filtered.length ? (
              filtered.map((t) => (
                <div key={t.id} className="tp-row">
                  <div className="c-name">{t.name}</div>
                  <div className="c-flags"><FlagsBadge flags={t.flags} /></div>
                  <div className="c-meta">
                    {t.updated_at
                      ? new Date(t.updated_at * 1000).toLocaleString()
                      : ""}
                  </div>
                  <div className="c-actions">
                    <RowActions
                      busy={busyId === t.id}
                      onEdit={() => openEdit(t)}
                      onDelete={() => onDelete(t.id)}
                    />
                  </div>
                </div>
              ))
            ) : (
              <div className="tp-empty">   </div>
            )}
          </div>

          {modalOpen && (
            <div className="tp-modal">
              <div className="tp-modal__dialog card">
                <div className="tp-modal__head">
                  <h2>{form.id ? " " : " "}</h2>
                  <button className="btn xs ghost" onClick={() => setModalOpen(false)}></button>
                </div>

                <form onSubmit={onSubmit} className="tp-modal__body">
                  <VoiceInput endpoint="/templates/voice" onResult={handleVoiceTemplate} />
                  <label className="field">
                    <span> *</span>
                    <input
                      className="input"
                      value={form.name}
                      onChange={(e) => setForm((s) => ({ ...s, name: e.target.value }))}
                      placeholder=":  "
                      required
                    />
                  </label>

                  <div className="group">
                    <div className="group-title"> </div>

                        <label className="field">
                          <span> </span>
                          <input
                            className="input"
                            value={form.payload.title}
                            onChange={(e) => setPayload("title", e.target.value)}
                          />
                        </label>

                        <label className="field">
                          <span> </span>
                          <textarea
                            className="input"
                            rows={3}
                            value={form.payload.expected_result}
                            onChange={(e) => setPayload("expected_result", e.target.value)}
                          />
                        </label>

                        <label className="field">
                          <span></span>
                          <input
                            className="input"
                            value={form.payload.result}
                            onChange={(e) => setPayload("result", e.target.value)}
                          />
                        </label>

                        <label className="field">
                          <span></span>
                          <select
                            className="input"
                            value={form.payload.type}
                            onChange={(e) => setPayload("type", e.target.value)}
                          >
                            <option value=" "> - </option>
                            <option value=" "> -  </option>
                            <option value=" "> - </option>
                            <option value=" "> - </option>
                          </select>
                        </label>

                    <div className="row2">
                          <label className="field">
                            <span> </span>
                            <input
                              className="input"
                              type="time"
                              value={form.payload.planned_time}
                              onChange={(e) => setPayload("planned_time", e.target.value)}
                            />
                          </label>

                          <label className="field">
                            <span> </span>
                            <input
                              className="input"
                              type="time"
                              value={form.payload.actual_time}
                              onChange={(e) => setPayload("actual_time", e.target.value)}
                            />
                          </label>
                        </div>

                        <label className="field">
                          <span> </span>
                          <input
                            className="input"
                            value={form.payload.manager}
                            onChange={(e) => setPayload("manager", e.target.value)}
                          />
                        </label>

                        <label className="field">
                          <span></span>
                          <input
                            className="input"
                            value={form.payload.comments}
                            onChange={(e) => setPayload("comments", e.target.value)}
                          />
                        </label>

                        <label className="field">
                          <span></span>
                          <input
                            className="input"
                            value={form.payload.responsible}
                            onChange={(e) => setPayload("responsible", e.target.value)}
                          />
                        </label>

                        <div className="row2">
                          <label className="field">
                            <span> </span>
                            <input
                              className="input"
                              type="date"
                              value={form.payload.start_date}
                              onChange={(e) => setPayload("start_date", e.target.value)}
                            />
                          </label>

                          <label className="field">
                            <span> </span>
                            <input
                              className="input"
                              type="date"
                              value={form.payload.end_date}
                              onChange={(e) => setPayload("end_date", e.target.value)}
                            />
                          </label>
                        </div>

                        <label className="field">
                          <span></span>
                          <input
                            className="input"
                            value={form.payload.tags}
                            onChange={(e) => setPayload("tags", e.target.value)}
                            placeholder="tag1, tag2"
                          />
                        </label>

                        <label className="field checkbox">
                          <input
                            type="checkbox"
                            checked={form.payload.enabled}
                            onChange={(e) => setPayload("enabled", e.target.checked)}
                          />
                          <span></span>
                        </label>

                        <label className="field">
                          <span></span>
                          <select
                            className="input"
                            value={form.payload.resultId}
                            onChange={(e) => setPayload("resultId", e.target.value)}
                          >
                            <option value=""></option>
                            {results.map((r) => (
                              <option key={r.id} value={r.id}>
                                {r.title}
                              </option>
                            ))}
                          </select>
                        </label>

                        <div className="row2">
                          <label className="field">
                            <span></span>
                            <select
                              className="input"
                              value={form.payload.repeat.type}
                              onChange={(e) =>
                                setPayload("repeat", {
                                  ...form.payload.repeat,
                                  type: e.target.value,
                                })
                              }
                            >
                              <option value="none"> </option>
                              <option value="daily"></option>
                              <option value="weekly"></option>
                              <option value="monthly"></option>
                              <option value="yearly"></option>
                              <option value="custom"> N </option>
                            </select>
                          </label>

                          {form.payload.repeat.type === "custom" && (
                            <label className="field">
                              <span> </span>
                              <input
                                className="input"
                                type="number"
                                min="2"
                                value={form.payload.repeat.interval}
                                onChange={(e) =>
                                  setPayload("repeat", {
                                    ...form.payload.repeat,
                                    interval: Number(e.target.value),
                                  })
                                }
                              />
                            </label>
                          )}
                        </div>
                      </div>

                  {error && <div className="tp-error">{error}</div>}

                  <div className="tp-modal__foot">
                    <button className="btn ghost" type="button" onClick={() => setModalOpen(false)}>
                      
                    </button>
                    <button className="btn primary" type="submit" disabled={busyId !== null}>
                      {form.id ? "" : ""}
                    </button>
                  </div>
                </form>
              </div>
            </div>
          )}
        </div>

        <aside className="tp-side">
          <button className="btn" onClick={openCreate}>   </button>
          <div className="tp-results-list">
            {results.map((r) => (
              <button key={r.id} className="tp-result" onClick={() => openCreateFromResult(r)}>
                {r.title}
              </button>
            ))}
          </div>
        </aside>
      </div>
    </Layout>
  );
}

