import React, { useState } from "react";
import api from "../../../services/api";
import "./TaskComments.css";

export default function TaskComments({
    taskId,
    author = "",
    comments = [],
    onCommentsChange,
}) {
    const [newComment, setNewComment] = useState("");
    const [replyTo, setReplyTo] = useState(null); // ID   ,   
    const [replyText, setReplyText] = useState("");

    //   
    const handleAddMainComment = async () => {
        if (!newComment.trim()) return;
        const updated = [...comments, { author, text: newComment, replies: [] }];
        try {
            await api.patch(`/task/update-field?id=${taskId}`, {
                field: "comments",
                value: JSON.stringify(updated),
            });
            onCommentsChange?.(updated);
            setNewComment("");
        } catch (e) {
            console.error("  :", e);
        }
    };

    //  
    const handleAddReply = async (parentId) => {
        if (!replyText.trim()) return;
        const updated = [...comments];
        if (updated[parentId]) {
            const replies = updated[parentId].replies || [];
            updated[parentId] = {
                ...updated[parentId],
                replies: [...replies, { author, text: replyText }],
            };
        }
        try {
            await api.patch(`/task/update-field?id=${taskId}`, {
                field: "comments",
                value: JSON.stringify(updated),
            });
            onCommentsChange?.(updated);
            setReplyText("");
            setReplyTo(null); //   
        } catch (e) {
            console.error("  :", e);
        }
    };

    return (
        <div className="task-comments">
            <h4>:</h4>

            {/*   */}
            {comments.length === 0 && <p>  </p>}
            {comments.map((c, idx) => (
                <div key={idx} className="comment-item">
                    <div className="comment-author">{c.author}</div>
                    <div className="comment-text">{c.text}</div>

                    {/*    */}
                    {c.replies && c.replies.length > 0 && (
                        <div className="comment-replies">
                            {c.replies.map((r, ridx) => (
                                <div key={ridx} className="comment-reply">
                                    <div className="comment-author">{r.author}</div>
                                    <div className="comment-text">{r.text}</div>
                                </div>
                            ))}
                        </div>
                    )}

                    {/*   */}
                    <button
                        className="reply-btn"
                        onClick={() => setReplyTo(replyTo === idx ? null : idx)}
                    >
                        {replyTo === idx ? "" : ""}
                    </button>

                    {/*       */}
                    {replyTo === idx && (
                        <div className="reply-form">
                            <input
                                type="text"
                                placeholder=" ..."
                                value={replyText}
                                onChange={(e) => setReplyText(e.target.value)}
                            />
                            <button onClick={() => handleAddReply(idx)}>
                                 
                            </button>
                        </div>
                    )}
                </div>
            ))}

            {/*     */}
            <div className="add-comment">
                <input
                    type="text"
                    placeholder="  ..."
                    value={newComment}
                    onChange={(e) => setNewComment(e.target.value)}
                />
                <button onClick={handleAddMainComment}></button>
            </div>
        </div>
    );
}
